import React, { Component } from 'react';
//import { NavLink } from 'react-router-dom';
//import * as router from 'react-router-dom';
import PropTypes from 'prop-types';
import {
    CToggler,
    CHeaderBrand,
    CHeaderNav,
    CHeaderSubheader,
    CBreadcrumb,
    CLink
} from '@coreui/react';
import { CIcon } from '@coreui/icons-react';

// adminRoutes config
import adminRoutes from '../../routes/adminRoutes';

import DefaultHeaderDropdown from './DefaultHeaderDropdown';
import logo from '../../assets/brand/logo-horizontal.png';
import { userActions } from '../../_actions';
import { connect } from 'react-redux';

const propTypes = {
    children: PropTypes.node
};

const defaultProps = {};

class DefaultHeader extends Component {
    render() {
        // eslint-disable-next-line
        const { toggleSidebarMobile, toggleSidebar, toggleTheme, user, logout } = this.props;

        return (
            <>
                <CToggler inHeader className="ml-3 d-lg-none" toggle={toggleSidebarMobile} />
                <CToggler inHeader className="ml-3 d-md-down-none" toggle={toggleSidebar} />
                <CLink href="https://coreui.io" target="_blank">
                    <CHeaderBrand
                        className="mx-auto d-lg-none"
                        src={logo}
                        width="97"
                        height="46"
                        alt="Logo"
                    />
                </CLink>

                <CHeaderNav className="d-md-down-none mr-auto"></CHeaderNav>

                <CHeaderNav>
                    <CToggler
                        custom
                        inHeader
                        className="ml-3 d-md-down-none"
                        toggle={toggleTheme}
                        title="Toggle Light/Dark Mode">
                        <CIcon name="cil-moon" className="c-d-dark-none" alt="CoreUI Icons Moon" />
                        <CIcon name="cil-sun" className="c-d-default-none" alt="CoreUI Icons Sun" />
                    </CToggler>
                    <DefaultHeaderDropdown onLogout={logout} user={user} />
                </CHeaderNav>

                <CHeaderSubheader className="px-3 justify-content-between">
                    <CBreadcrumb className="border-0 c-subheader-nav" appRoutes={adminRoutes} />
                </CHeaderSubheader>
            </>
        );
    }
}

DefaultHeader.propTypes = propTypes;
DefaultHeader.defaultProps = defaultProps;

function mapState(state) {
    const { authentication } = state;
    return { user: authentication.user };
}

const actionCreators = {
    logout: userActions.logout
};

export default connect(mapState, actionCreators)(DefaultHeader);
