import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { CDropdown, CDropdownItem, CDropdownMenu, CDropdownToggle, CImg } from '@coreui/react';
import { CIcon } from '@coreui/icons-react';
import { Link } from 'react-router-dom';
import userAvatar from '../../assets/avatars/avatar1.jpg';

const propTypes = {
    notif: PropTypes.bool,
    accnt: PropTypes.bool,
    tasks: PropTypes.bool,
    mssgs: PropTypes.bool
};
const defaultProps = {
    notif: false,
    accnt: false,
    tasks: false,
    mssgs: false
};

class DefaultHeaderDropdown extends Component {
    constructor(props) {
        super(props);

        this.toggle = this.toggle.bind(this);
        this.state = {
            dropdownOpen: false
        };
    }

    toggle() {
        this.setState({
            dropdownOpen: !this.state.dropdownOpen
        });
    }

    dropAccnt(account, onLogout) {
        return (
            <CDropdown inNav className="c-header-nav-items mx-2" direction="down">
                <CDropdownToggle nav header>
                    <div className="c-avatar">
                        <CImg src={userAvatar} className="c-avatar-img" alt={account.username} />
                    </div>
                </CDropdownToggle>
                <CDropdownMenu className="pt-0" right>
                    <CDropdownItem>
                        <Link to={`/admin/profile`}>
                            <CIcon name="cil-user" className="mr-2" /> Profile
                        </Link>
                    </CDropdownItem>
                    {/*<CDropdownItem><CIcon name="cil-settings" className="mr-2"/> Settings</CDropdownItem>*/}
                    <CDropdownItem divider />
                    {/*<CDropdownItem><CIcon name="cil-lock-locked" className="mr-2"/> Lock Account</CDropdownItem>*/}
                    <CDropdownItem onClick={onLogout}>
                        <CIcon name="cil-account-logout" className="mr-2" /> Logout
                    </CDropdownItem>
                </CDropdownMenu>
            </CDropdown>
        );
    }

    render() {
        let { user, onLogout } = this.props;
        return user ? this.dropAccnt(user, onLogout) : null;
    }
}

DefaultHeaderDropdown.propTypes = propTypes;
DefaultHeaderDropdown.defaultProps = defaultProps;

export default DefaultHeaderDropdown;
